.profileContainer {
    display: flex;
    height: 100vh;
    width: 100vw;
    position: relative;
    max-width: 100%;
  }
  .profileContainer > hr{
    width: 1px;
  }
  .profileContainer > div {
    display: flex;
    height: 100vh;
    width: 100vw;
    max-width: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .profileContainer > div:first-child > h1 {
    color: rgba(0, 0, 0, 0.555);
    font: 500 2.2vmax "Montserrat";
    transform: translateX(-10vmax) translateY(-2vmax);
  }
  
  .profileContainer > div:first-child > img {
    width: 20vmax;
    height: 20vmax;
    border-radius: 50%;
    object-fit: cover;
    transition: all 0.5s;
  }
  
  .profileContainer > div:first-child > img:hover {
    transform: scale(1.05);
  }
  
  .profileContainer > div:first-child > a {
   
    font: 400 1vmax "Montserrat";
    text-decoration: none;
    padding: 0.5vmax;
    width: 30%;
    margin: 4vmax;
    text-align: center;
    transition: all 0.5s;
  }
  
  
  .profileContainer > div:last-child {
    justify-content: space-evenly;
    align-items: flex-start;
    padding: 5vmax;
    box-sizing: border-box;
  }
  
  .profileContainer > div:last-child > div > h4 {
    color: black;
    font: 400 1.2vmax "Montserrat";
  }
  
  .profileContainer > div:last-child > div > p {
    color: rgba(0, 0, 0, 0.418);
    font: 400 1vmax 'Montserrat';
    margin: 0.2vmax;
  }
  
  .profileContainer > div:last-child > div:last-child {
    display: flex;
    flex-direction: column;
    width: 60%;
  }
  
  .profileContainer > div:last-child > div:last-child > a {

    font: 400 1vmax "Montserrat";

    text-decoration: none;
    padding: 0.5vmax;
    text-align: center;
    transition: all 0.5s;
    margin: 1vmax 0;
  }
  
  @media screen and (max-width: 600px) {
    .profileContainer > hr{
      width: 90%;
    }
    .profileContainer {
      margin-top: 1rem;
      flex-direction: column;
    }
  
    .profileContainer > div:first-child > h1 {
      font: 500 3.2vmax "Montserrat";
      transform: translateY(-2vmax);
    }
  
    .profileContainer > div:first-child > a {
      font: 400 1.7vmax "Montserrat";
      padding: 1vmax;
    }
  
    .profileContainer > div:last-child {
      text-align: center;
      align-items: center;
    }
  
    .profileContainer > div:last-child > div > h4 {
      font: 400 2.5vmax "Montserrat";
    }
  
    .profileContainer > div:last-child > div > p {
      font: 400 2vmax 'Montserrat';
      margin: 0.5vmax 0;
    }
  
    .profileContainer > div:last-child > div:last-child > a {
      font: 400 1.8vmax "Montserrat";
      padding: 1vmax;
      margin: 2vmax 0;
    }
  }